// src/pages/ProblemDetail.jsx
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import {
  doc,
  getDoc,
} from "firebase/firestore";
import { db } from "../firebase/firebase";

export default function ProblemDetail() {
  const { id } = useParams();
  const [problem, setProblem] = useState(null);
  const [loading, setLoading] = useState(true);
  const [code, setCode] = useState("// Write your solution here");
  const [result, setResult] = useState("");

  useEffect(() => {
    const fetchProblem = async () => {
      try {
        const docRef = doc(db, "problems", id);
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          setProblem({ id: docSnap.id, ...docSnap.data() });
        } else {
          setProblem(null);
        }
      } catch (err) {
        console.error("Error fetching problem:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchProblem();
  }, [id]);

  const handleSubmit = () => {
    // Dummy logic: we just check if code contains the expected keyword
    if (code.toLowerCase().includes(problem.expectedKeyword.toLowerCase())) {
      setResult("✅ Passed!");
    } else {
      setResult("❌ Try Again");
    }
  };

  if (loading) return <div className="text-white text-center p-6">Loading...</div>;

  if (!problem) return <div className="text-red-400 p-6 text-center">Problem not found.</div>;
  

  return (
    <div className="min-h-screen bg-gray-950 text-white p-6 max-w-4xl mx-auto">
      <h2 className="text-3xl font-bold text-green-400 mb-4">{problem.title}</h2>
      <p className="mb-2 text-lg">{problem.description}</p>
      <p className="text-sm text-yellow-400 mb-4">Difficulty: {problem.difficulty}</p>

      {/* Code Editor (for now a textarea) */}
      <textarea
        value={code}
        onChange={(e) => setCode(e.target.value)}
        rows={10}
        className="w-full p-3 bg-gray-800 text-white border border-green-400 rounded mb-4"
      ></textarea>

      <button
        onClick={handleSubmit}
        className="bg-green-500 text-black px-4 py-2 rounded hover:bg-green-600 transition"
      >
        Submit
      </button>

      {result && <div className="mt-4 text-lg">{result}</div>}
    </div>
  );
}
