import React, { useEffect, useState } from "react";
import { collection, getDocs } from "firebase/firestore";
import { db } from "../firebase/firebase";

export default function Leaderboard() {
  const [users, setUsers] = useState([]);

  useEffect(() => {
    const fetchLeaderboard = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, "users"));
        const usersData = querySnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));

        // Sort descending by problemsSolved
        usersData.sort((a, b) => (b.problemsSolved || 0) - (a.problemsSolved || 0));
        setUsers(usersData);
      } catch (error) {
        console.error("Error fetching leaderboard:", error);
      }
    };

    fetchLeaderboard();
  }, []);

  return (
    <div className="min-h-screen bg-gray-950 text-white p-4">
      <div className="max-w-4xl mx-auto mt-10">
        <h2 className="text-3xl font-bold text-green-400 mb-6">Leaderboard</h2>

        <table className="w-full text-left border-collapse border border-green-400">
          <thead className="bg-gray-800">
            <tr>
              <th className="p-3 border border-green-400">Rank</th>
              <th className="p-3 border border-green-400">Name</th>
              <th className="p-3 border border-green-400">Email</th>
              <th className="p-3 border border-green-400">Problems Solved</th>
            </tr>
          </thead>
          <tbody>
            {users.map((user, index) => (
              <tr key={user.id} className="hover:bg-gray-800 transition">
                <td className="p-3 border border-green-400">{index + 1}</td>
                <td className="p-3 border border-green-400">{user.name || "Anonymous"}</td>
                <td className="p-3 border border-green-400">{user.email}</td>
                <td className="p-3 border border-green-400">{user.problemsSolved || 0}</td>
              </tr>
            ))}
          </tbody>
        </table>

        {users.length === 0 && (
          <p className="mt-4 text-red-400">No users found.</p>
        )}
      </div>
    </div>
  );
}
