// src/pages/Problems.jsx
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { collection, getDocs } from "firebase/firestore";
import { db } from "../firebase/firebase";

export default function Problems() {
  const [problems, setProblems] = useState([]);
  const [filteredProblems, setFilteredProblems] = useState([]);
  const [selectedDifficulty, setSelectedDifficulty] = useState("All");
  const [searchTerm, setSearchTerm] = useState("");

  useEffect(() => {
    const fetchProblems = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, "problems"));
        const problemsData = querySnapshot.docs.map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }));
        console.log("✅ Fetched problems:", problemsData); // Debug log
        setProblems(problemsData);
        setFilteredProblems(problemsData);
      } catch (error) {
        console.error("❌ Error fetching problems:", error);
      }
    };
  
    fetchProblems();
  }, []);
  
  // Filter logic
  useEffect(() => {
    let temp = [...problems];

    if (selectedDifficulty !== "All") {
      temp = temp.filter((p) => p.difficulty === selectedDifficulty);
    }

    if (searchTerm !== "") {
      temp = temp.filter((p) =>
        p.title.toLowerCase().includes(searchTerm.toLowerCase())
      );
    }

    setFilteredProblems(temp);
  }, [selectedDifficulty, searchTerm, problems]);

  return (
    <div className="min-h-screen bg-gray-950 text-white p-4">
      <div className="max-w-4xl mx-auto mt-10">
        <h2 className="text-3xl font-bold text-green-400 mb-6">Problems</h2>

        {/* Filters */}
        <div className="mb-4 flex gap-2 flex-wrap">
          {["All", "Easy", "Medium", "Hard"].map((level) => (
            <button
              key={level}
              onClick={() => setSelectedDifficulty(level)}
              className={`px-4 py-1 rounded-full border ${
                selectedDifficulty === level
                  ? "bg-green-400 text-black font-bold"
                  : "border-green-400 text-green-400"
              }`}
            >
              {level}
            </button>
          ))}

          <input
            type="text"
            placeholder="Search title..."
            className="ml-auto px-3 py-1 rounded bg-gray-800 text-white border border-green-400"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
        </div>

        {/* Problems Table */}
        <table className="w-full text-left border-collapse border border-green-400">
          <thead className="bg-gray-800">
            <tr>
              <th className="p-3 border border-green-400">Title</th>
              <th className="p-3 border border-green-400">Difficulty</th>
              <th className="p-3 border border-green-400">Action</th>
            </tr>
          </thead>
          <tbody>
            {filteredProblems.map((problem) => (
              <tr key={problem.id} className="hover:bg-gray-800 transition">
                <td className="p-3 border border-green-400">{problem.title}</td>
                <td
                  className={`p-3 border border-green-400 ${
                    problem.difficulty === "Easy"
                      ? "text-green-400"
                      : problem.difficulty === "Medium"
                      ? "text-yellow-400"
                      : "text-red-400"
                  }`}
                >
                  {problem.difficulty}
                </td>
                <td className="p-3 border border-green-400">
                  <Link
                    to={`/problems/${problem.id}`}
                    className="text-green-400 hover:underline"
                  >
                    Solve
                  </Link>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {filteredProblems.length === 0 && (
          <p className="mt-4 text-red-400">No problems found.</p>
        )}
      </div>
    </div>
  );
}
